































































































































































































































































































import { Vue, Component } from 'vue-property-decorator';
import { BaseLayoutComponentData } from '@/types/layouts/baseLayoutComponent.interface';
import { mapState } from 'vuex';

@Component({
  computed: {
    ...mapState([
      'title', // 
      'breadcrumbs', // 
      'overlay', // 
      'loading', //  / HTTP
      'skeletonLoader' // 
    ])
  },

  data(): BaseLayoutComponentData {
    return {
      drawer: null, // 
      miniVariant: false, // 
      expandOnHover: false, //  mini-variant
      navMenuItems: [] // 
    };
  }
})
export default class BaseLayoutAdmin extends Vue {
  title!: string; // 
  breadcrumbs!: object[]; // 
  overlay!: boolean; // 
  loading!: boolean; //  / HTTP
  skeletonLoader!: boolean; // 

  drawer!: boolean | null; // 
  miniVariant!: boolean; // 
  expandOnHover!: boolean; //  mini-variant
  navMenuItems!: object[]; // 

  //  /  / HTTP
  get pageLoading() {
    if (!this.overlay && !this.loading) {
      // /false
      return false;
    }
    return true;
  }
  // 
  get pageTitle() {
    return this.$route.meta.isDynamicTitle
      ? this.title
      : this.$route.meta.title;
  }
  // 
  get pageBreadcrumbs() {
    return this.$route.meta.isDynamicTitle
      ? this.breadcrumbs
      : this.$route.meta.breadcrumbs;
  }

  // 
  drawerActive() {
    if(this.$vuetify.breakpoint.lgAndUp) {
      this.miniVariant = !this.miniVariant;
    } else {
      this.miniVariant = false;
      this.drawer = !this.drawer;
    }
  }
  drawerHover(event: any) {
    // console.log(event.type)
    if (this.miniVariant) {
      switch (event.type) {
        case 'mouseover':
          this.expandOnHover = true;
          break;
        case 'mouseleave':
          this.expandOnHover = false;
          break;
      }
    }
  }
  // 
  fetchNavMenuItemsData() {
    this.navMenuItems = [
      {
        // icon: "mdi-shape-outline",
        icon: "D3III16glBasic icon-16gl-appsBig",
        // icon: "D3III16glMobius icon-16gl-B",
        title: "Basic",
        children: [
          { title: "Colors", path: "/components/colors" },
          { title: "Typography", path: "/components/typography" },
          { title: "Icons", path: "/components/icons" },
          { title: "Buttons", path: "/components/buttons" }
        ]
      },
      {
        // icon: "mdi-table-row",
        icon: "D3III16glBasic icon-16gl-rename",
        // icon: "D3III16glMobius icon-16gl-C",
        title: "Bars",
        children: [
          { title: "App bars: top", path: "/components/app-bars" },
          { title: "App bars: bottom", path: "/components/app-bars-bottom" }
        ]
      },
      {
        // icon: "mdi-menu",
        icon: "D3III16glBasic icon-16gl-mapMarker",
        // icon: "D3III16glMobius icon-16gl-D",
        title: "Navigation",
        children: [
          { title: "Navigation Drawer", path: "/components/navigation-drawer" },
          { title: "Bottom Navigation", path: "/components/bottom-navigation" },
          { title: "Tabs", path: "/components/tabs" },
          { title: "Menus", path: "/components/menus" },
          { title: "Breadcrumbs", path: "/components/breadcrumbs" }
        ]
      },
      {
        // icon: "mdi-clipboard-check-outline",
        icon: "D3III16glBasic icon-16gl-clipboardCheck",
        // icon: "D3III16glMobius icon-16gl-E",
        title: "Forms",
        children: [
          { title: "RadioButtons", path: "/components/radio-buttons" },
          { title: "Checkboxes", path: "/components/checkboxes" },
          { title: "Switches", path: "/components/switches" },
          { title: "TextFields", path: "/components/text-fields" },
          { title: "Textareas", path: "/components/textareas" },
          { title: "Selects", path: "/components/selects" }
        ]
      },
      {
        // icon: "mdi-table",
        icon: "D3III16glBasic icon-16gl-code",
        // icon: "D3III16glMobius icon-16gl-F",
        title: "Datas",
        children: [
          { title: "DataTables", path: "/components/data-tables" },
          { title: "Paginations", path: "/components/paginations" },
          { title: "Lists", path: "/components/lists" },
          { title: "Avatars", path: "/components/avatars" },
          { title: "Badges", path: "/components/badges" }
        ]
      },
      // {
      //   icon: "mdi-image-outline",
      //   title: "Images",
      //   children: [
      //     { title: "Images", path: "/components/images" },
      //     { title: "Color", path: "/components/" },
      //     { title: "Color", path: "/components/" }
      //   ]
      // },
      {
        // icon: "mdi-message-alert-outline",
        icon: "D3III16glBasic icon-16gl-bubblesDots2",
        // icon: "D3III16glMobius icon-16gl-G",
        title: "Notice",
        children: [
          { title: "Alerts", path: "/components/alerts" },
          { title: "Snackbars", path: "/components/snackbars" },
          { title: "Banners", path: "/components/banners" },
          { title: "Dialogs", path: "/components/dialogs" }
        ]
      },
      {
        // icon: "mdi-dots-horizontal",
        icon: "D3III16glBasic icon-16gl-ellipsis",
        // icon: "D3III16glMobius icon-16gl-H",
        title: "Others",
        children: [
          { title: "Cards", path: "/components/cards" },
          { title: "Chips", path: "/components/chips" },
          { title: "Images", path: "/components/images" },
          { title: "Tooltips", path: "/components/tooltips" }
        ]
      }
    ];
  }

  // // 
  // goToPage (item: any) {
  //   // 
  //   if (this.$route.path === item.path) {
  //     return
  //   }
  //   this.$router.push({
  //     path: item.path
  //   });
  // }

  // // 
  // pageYOffset = 0; // 
  // getPageYOffset() {
  //   // console.log(window.pageYOffset);
  //   this.pageYOffset =
  //     window.pageYOffset ||
  //     document.documentElement.scrollTop ||
  //     document.body.scrollTop;
  // }

  // 
  created() {
    this.fetchNavMenuItemsData();
  }
}
